dl-shadow--3dp {\n  @include shadow-3dp();\n}\n\n.mdl-shadow--4dp {\n  @include shadow-4dp();\n}\n\n.mdl-shadow--6dp {\n  @include shadow-6dp();\n}\n\n.mdl-shadow--8dp {\n  @include shadow-8dp();\n}\n\n.mdl-shadow--16dp {\n  @include shadow-16dp();\n}\n\n.mdl-shadow--24dp {\n  @include shadow-24dp();\n}\n","/**\n * Copyright 2015 Google Inc. All Rights Reserved.\n *\n * Licensed under the Apache License, Version 2.0 (the \"License\");\n * you may not use this file except in compliance with the License.\n * You may obtain a copy of the License at\n *\n *      http://www.apache.org/licenses/LICENSE-2.0\n *\n * Unless required by applicable law or agreed to in writing, software\n * distributed under the License is distributed on an \"AS IS\" BASIS,\n * WITHOUT WARRANTIES OR CONDITIONS OF ANY KIND, either express or implied.\n * See the License for the specific language governing permissions and\n * limitations under the License.\n */\n\n/*\n* NOTE: Some rules here are applied using duplicate selectors.\n* This is on purpose to increase their specificity when applied.\n* For example: `.mdl-cell--1-col-phone.mdl-cell--1-col-phone`\n*/\n\n@import \"../variables\";\n\n.mdl-grid {\n  display: flex;\n  flex-flow: row wrap;\n  margin: 0 auto 0 auto;\n  align-items: stretch;\n\n  &.mdl-grid--no-spacing {\n    padding: 0;\n  }\n}\n\n.mdl-cell {\n  box-sizing: border-box;\n}\n\n\n.mdl-cell--top {\n  align-self: flex-start;\n}\n\n.mdl-cell--middle {\n  align-self: center;\n}\n\n.mdl-cell--bottom {\n  align-self: flex-end;\n}\n\n.mdl-cell--stretch {\n  align-self: stretch;\n}\n\n.mdl-grid.mdl-grid--no-spacing > .mdl-cell {\n  margin: 0;\n}\n\n// Define order override classes.\n@for $i from 1 through $grid-max-columns {\n  .mdl-cell--order-#{$i} {\n    order: $i;\n  }\n}\n\n\n// Mixins for width calculation.\n@mixin partial-size($size, $columns, $gutter) {\n  width: calc(#{(($size / $columns) * 100)+\"%\"} - #{$gutter});\n\n  .mdl-grid--no-spacing > & {\n    width: #{(($size / $columns) * 100)+\"%\"};\n  }\n}\n\n@mixin full-size($gutter) {\n  @include partial-size(1, 1, $gutter);\n}\n\n@mixin offset-size($size, $columns, $gutter) {\n  margin-left: calc(#{(($size / $columns) * 100)+\"%\"} + #{$gutter / 2});\n\n  .mdl-grid.mdl-grid--no-spacing > & {\n    margin-left: #{(($size / $columns) * 100)+\"%\"};\n  }\n}\n\n\n\n////////// Phone //////////\n\n@media (max-width: $grid-tablet-breakpoint - 1) {\n  .mdl-grid {\n    padding: $grid-phone-margin - ($grid-phone-gutter / 2);\n  }\n\n  .mdl-cell {\n    margin: $grid-phone-gutter / 2;\n    @include partial-size($grid-cell-default-columns, $grid-phone-columns,\n        $grid-phone-gutter);\n  }\n\n  .mdl-cell--hide-phone {\n    display: none !important;\n  }\n\n  // Define order override classes.\n  @for $i from 1 through $grid-max-columns {\n    .mdl-cell--order-#{$i}-phone.mdl-cell--order-#{$i}-phone {\n      order: $i;\n    }\n  }\n\n  // Define partial sizes for columnNumber < totalColumns.\n  @for $i from 1 through ($grid-phone-columns - 1) {\n    .mdl-cell--#{$i}-col,\n    .mdl-cell--#{$i}-col-phone.mdl-cell--#{$i}-col-phone {\n      @include partial-size($i, $grid-phone-columns, $grid-phone-gutter);\n    }\n  }\n\n  // Define 100% for everything else.\n  @for $i from $grid-phone-columns through $grid-desktop-columns {\n    .mdl-cell--#{$i}-col,\n    .mdl-cell--#{$i}-col-phone.mdl-cell--#{$i}-col-phone {\n      @include full-size($grid-phone-gutter);\n    }\n  }\n\n  // Define valid phone offsets.\n  @for $i from 1 through ($grid-phone-columns - 1) {\n    .mdl-cell--#{$i}-offset,\n    .mdl-cell--#{$i}-offset-phone.mdl-cell--#{$i}-offset-phone {\n      @include offset-size($i, $grid-phone-columns, $grid-phone-gutter);\n    }\n  }\n}\n\n\n////////// Tablet //////////\n\n@media (min-width: $grid-tablet-breakpoint) and (max-width: $grid-desktop-breakpoint - 1) {\n  .mdl-grid {\n    padding: $grid-tablet-margin - ($grid-tablet-gutter / 2);\n  }\n\n  .mdl-cell {\n    margin: $grid-tablet-gutter / 2;\n    @include partial-size($grid-cell-default-columns, $grid-tablet-columns,\n        $grid-tablet-gutter);\n  }\n\n  .mdl-cell--hide-tablet {\n    display: none !important;\n  }\n\n  // Define order override classes.\n  @for $i from 1 through $grid-max-columns {\n    .mdl-cell--order-#{$i}-tablet.mdl-cell--order-#{$i}-tablet {\n      order: $i;\n    }\n  }\n\n  // Define partial sizes for columnNumber < totalColumns.\n  @for $i from 1 through ($grid-tablet-columns - 1) {\n    .mdl-cell--#{$i}-col,\n    .mdl-cell--#{$i}-col-tablet.mdl-cell--#{$i}-col-tablet {\n      @include partial-size($i, $grid-tablet-columns, $grid-tablet-gutter);\n    }\n  }\n\n  // Define 100% for everything else.\n  @for $i from $grid-tablet-columns through $grid-desktop-columns {\n    .mdl-cell--#{$i}-col,\n    .mdl-cell--#{$i}-col-tablet.mdl-cell--#{$i}-col-tablet {\n      @include full-size($grid-tablet-gutter);\n    }\n  }\n\n  // Define valid tablet offsets.\n  @for $i from 1 through ($grid-tablet-columns - 1) {\n    .mdl-cell--#{$i}-offset,\n    .mdl-cell--#{$i}-offset-tablet.mdl-cell--#{$i}-offset-tablet {\n      @include offset-size($i, $grid-tablet-columns, $grid-tablet-gutter);\n    }\n  }\n}\n\n\n////////// Desktop //////////\n\n@media (min-width: $grid-desktop-breakpoint) {\n  .mdl-grid {\n    padding: $grid-desktop-margin - ($grid-desktop-gutter / 2);\n  }\n\n  .mdl-cell {\n    margin: $grid-desktop-gutter / 2;\n    @include partial-size($grid-cell-default-columns, $grid-desktop-columns,\n        $grid-desktop-gutter);\n  }\n\n  .mdl-cell--hide-desktop {\n    display: none !important;\n  }\n\n  // Define order override classes.\n  @for $i from 1 through $grid-max-columns {\n    .mdl-cell--order-#{$i}-desktop.mdl-cell--order-#{$i}-desktop {\n      order: $i;\n    }\n  }\n\n  // Define partial sizes for all numbers of columns.\n  @for $i from 1 through $grid-desktop-columns {\n    .mdl-cell--#{$i}-col,\n    .mdl-cell--#{$i}-col-desktop.mdl-cell--#{$i}-col-desktop {\n      @include partial-size($i, $grid-desktop-columns, $grid-desktop-gutter);\n    }\n  }\n\n  // Define valid desktop offsets.\n  @for $i from 1 through ($grid-desktop-columns - 1) {\n    .mdl-cell--#{$i}-offset,\n    .mdl-cell--#{$i}-offset-desktop.mdl-cell--#{$i}-offset-desktop {\n      @include offset-size($i, $grid-desktop-columns, $grid-desktop-gutter);\n    }\n  }\n}\n","@import '~theme/helpers';\n\nmaterial-angular-select {\n  .material-angular-select {\n    width: 100%;\n\n    .mdl-textfield__input:focus {\n      outline: none;\n    }\n\n    .is-item-hover {\n      background-color: $default-item-hover-bg-color;\n    }\n\n    &.is-focused {\n      .mdl-icon-toggle__label {\n        color: $color-primary;\n      }\n    }\n\n    .mdl-menu__item {\n      color: $text-color-primary;\n    }\n\n    // custom scroll\n    ::-webkit-scrollbar {\n      width: 0.5rem;\n      height: 0.5rem;\n    }\n\n    ::-webkit-scrollbar-thumb {\n      background: $scrollbar-thumb;\n      cursor: pointer;\n    }\n\n    ::-webkit-scrollbar-track {\n      background: $scrollbar-track;\n    }\n\n    .mdl-icon-toggle__label {\n      color: $text-color-primary;\n    }\n  }\n}\n","@import '~theme/helpers';\n\nhtml {\n  color: $text-color-primary;\n}\n\n.mdl-layout {\n  background: $layout-drawer-bg-color;\n  overflow: hidden;\n  min-height: 100vh;\n}\n\nmain {\n  background: url(\"#{$image-path}/Dark_background_1920x1080.png\") center top no-repeat;\n  background-size: cover;\n}\n\n.mdl-layout__header {\n  width: 100% !important;\n  margin-left: 0 !important;\n}\n\n.mdl-layout__drawer {\n  border: none;\n\n  header {\n    text-align: center;\n    text-transform: uppercase;\n    color: $layout-drawer-header-color;\n    height: $layout-header-desktop-row-height;\n    line-height: $layout-header-desktop-row-height;\n    background: $layout-drawer-header-bg-color;\n    font-size: 25px;\n    font-weight: 500;\n    letter-spacing: 1px;\n  }\n\n  .mdl-navigation {\n    flex-grow: 1;\n    padding-top: 48px;\n    padding-bottom: 8px;\n    bottom: 0;\n\n    hr {\n      border-top: 1px solid $list-border-color;\n      margin: 8px 0;\n    }\n\n    .mdl-navigation__link {\n      @include typo-layout-navigation-link;\n\n      padding: 12px 0;\n      position: relative;\n      border-left: 2px solid transparent;\n\n      .material-icons {\n        margin: -3px 25px 0 23px;\n        color: $layout-drawer-navigation-link-icon-color;\n      }\n\n      &--current {\n        .material-icons {\n          color: $layout-drawer-navigation-link-active-color;\n        }\n\n        border-left: 2px solid $layout-drawer-navigation-link-active-color;\n      }\n\n      &:hover {\n        color: $layout-drawer-navigation-link-active-color;\n\n        .material-icons {\n          color: $layout-drawer-navigation-link-active-color;\n        }\n      }\n    }\n\n    .sub-navigation {\n      cursor: pointer;\n\n      .mdl-navigation__link {\n        i:last-child {\n          margin: 0 16px 0 0;\n          transition: transform 0.3s;\n          float: right;\n        }\n      }\n\n      .mdl-navigation {\n        display: none;\n        padding: 5px 0;\n\n        .mdl-navigation__link {\n          padding-left: 72px;\n\n          &--current {\n            border-left: none;\n          }\n        }\n      }\n\n      &--show {\n        .mdl-navigation__link i:last-child {\n          transform: rotate(180deg);\n        }\n\n        .mdl-navigation {\n          display: block;\n        }\n      }\n    }\n  }\n}\n\n.mdl-layout__header-row {\n  color: $dropdown-menu-header-font-color;\n\n  .avatar-dropdown {\n    cursor: pointer;\n    margin: 0 10px;\n    padding: 0 20px;\n    line-height: $layout-header-desktop-row-height;\n\n    &:hover {\n      background: $layout-header-icon-hover-color;\n    }\n\n    & > img {\n      width: $layout-header-icon-size;\n      height: $layout-header-icon-size;\n    }\n\n    & > span {\n      color: $layout-header-name-color;\n      padding-right: 16px;\n    }\n  }\n\n  .mdl-textfield {\n    padding: 18px 0;\n  }\n\n  .mdl-button--icon {\n    &:hover {\n      background: $layout-header-icon-hover-color;\n    }\n  }\n\n  .material-icons.mdl-badge {\n    margin: 10px;\n    cursor: pointer;\n    overflow: visible;\n    text-align: center;\n    height: $button-fab-font-size;\n    width: $button-fab-font-size;\n    min-width: 0;\n    padding: 4px 4px;\n\n    &:hover {\n      background: $layout-header-icon-hover-color;\n    }\n\n    &.mdl-badge--overlap {\n      &::after {\n        top: -3px;\n        right: -8px;\n      }\n    }\n  }\n}\n\n// custom scroll\n::-webkit-scrollbar {\n  width: 0.5rem;\n  height: 0.5rem;\n}\n\n::-webkit-scrollbar-thumb {\n  background: $scrollbar-thumb;\n  cursor: pointer;\n}\n\n::-webkit-scrollbar-track {\n  background: $scrollbar-track;\n}\n\n@media screen and (max-width: $layout-screen-size-threshold) {\n  .mdl-layout--fixed-drawer {\n    main {\n      margin-right: 0;\n    }\n\n    & > .mdl-layout__content {\n      margin-left: 0;\n    }\n\n    & > .mdl-layout__drawer {\n      transform: translateX(-$layout-drawer-width - 10px);\n\n      header {\n        height: $layout-header-mobile-row-height;\n        line-height: $layout-header-mobile-row-height;\n      }\n\n      &.is-visible {\n        transform: translateX(0) !important;\n      }\n    }\n  }\n\n  .mdl-layout__header-row {\n    .avatar-dropdown {\n      line-height: $layout-header-mobile-row-height;\n    }\n  }\n}\n\n@media screen and (max-width: $phone-breakpoint - 1px) {\n  .mdl-grid > .mdl-grid .mdl-cell {\n    width: 100% !important;\n  }\n\n  .mdl-layout__header-row {\n    .message,\n    .notification {\n      display: none;\n    }\n  }\n}\n","@mixin typo-default($usePreferred: true) {\n  @include typo-preferred-font($usePreferred);\n\n  font-size: 16px;\n  font-weight: 300;\n}\n\n@mixin typo-list($usePreferred: true) {\n  @include typo-default($usePreferred);\n\n  color: $color-white;\n  letter-spacing: 0;\n}\n\n@mixin typo-card-supporting-text($usePreferred: true) {\n  @include typo-default($usePreferred);\n}\n\n@mixin typo-card-subtitle-text($usePreferred: true) {\n  @include typo-default($usePreferred);\n}\n\n@mixin typo-textfield-input($usePreferred: true) {\n  @include typo-preferred-font($usePreferred);\n}\n\n@mixin typo-data-table($usePreferred: true) {\n  @include typo-preferred-font($usePreferred);\n\n  font-weight: 300;\n}\n\n@mixin typo-dropdown-menu-header($usePreferred: true) {\n  @include typo-preferred-font($usePreferred);\n\n  font-size: 1rem;\n  font-weight: 100;\n  color: $dropdown-menu-header-font-color;\n}\n\n@mixin typo-dropdown-menu-li($usePreferred: true) {\n  @include typo-preferred-font($usePreferred);\n\n  font-size: 1rem;\n  font-weight: 100;\n  color: $color-white;\n}\n\n@mixin typo-label($usePreferred: true) {\n  @include typo-preferred-font($usePreferred);\n\n  font-weight: 300;\n  line-height: 1em;\n}\n\n@mixin typo-message-topic($usePreferred: true) {\n  @include typo-preferred-font($usePreferred);\n\n  line-height: 1.1em;\n\n  span {\n    font-size: 12px;\n    font-weight: lighter;\n    color: rgba(255, 255, 255, 0.5);\n  }\n\n  span:first-of-type {\n    font-size: 15px;\n    font-weight: 500;\n    color: $color-white;\n  }\n}\n\n@mixin typo-account-dropdown-header($usePreferred: true) {\n  @include typo-preferred-font($usePreferred);\n\n  line-height: 1.1em;\n\n  span {\n    color: rgba(255, 255, 255, 0.64);\n    line-height: 1.3em;\n    font-size: 1rem;\n    font-weight: 300;\n  }\n\n  span:first-of-type {\n    line-height: 1.7em;\n    color: rgba(255, 255, 255, 0.8);\n    font-size: 18px;\n    font-weight: 300;\n  }\n}\n\n@mixin typo-piechart-title($usePreferred: true) {\n  @include typo-preferred-font($usePreferred);\n\n  fill: $color-white;\n  font-weight: 300;\n}\n\n@mixin typo-chart-legend($usePreferred: true) {\n  @include typo-preferred-font($usePreferred);\n\n  font-weight: 300;\n  font-size: 16px;\n  color: $color-white;\n}\n\n@mixin typo-linechart-axis-label($usePreferred: true) {\n  @include typo-preferred-font($usePreferred);\n\n  font-size: 12px;\n  font-weight: 300;\n}\n\n@mixin typo-weather-temperature($usePreferred: true) {\n  @include typo-preferred-font($usePreferred);\n\n  font-size: 100px;\n  line-height: 1;\n}\n\n@mixin typo-weather-description($usePreferred: true) {\n  @include typo-preferred-font($usePreferred);\n\n  font-size: 18px;\n  font-weight: 300;\n}\n\n@mixin typo-layout-navigation-link($usePreferred: true) {\n  @include typo-preferred-font($usePreferred);\n\n  font-size: 13px;\n  font-weight: 500;\n\n  .material-icons {\n    font-size: 22px;\n    width: 22px;\n  }\n}\n\n@mixin typo-employer-form-header($usePreferred: true) {\n  @include typo-preferred-font($usePreferred);\n\n  h2:first-of-type {\n    font-size: 24px;\n    font-weight: 400;\n    color: rgba(255, 255, 255, 0.8);\n    line-height: 1.2em;\n    margin-top: 0;\n    margin-bottom: 0;\n  }\n\n  .mdl-card__subtitle {\n    font-size: 13px;\n    font-weight: 300;\n    color: rgba(255, 255, 255, 0.2);\n    line-height: 1.2em;\n  }\n}\n\n@mixin typo-employer-form-topic($usePreferred: true) {\n  @include typo-preferred-font($usePreferred);\n\n  font-size: 20px;\n  font-weight: 300;\n  color: $employer-form-color-darker;\n}\n\n@mixin typo-employer-form-inputs($usePreferred: true) {\n  @include typo-preferred-font($usePreferred);\n\n  color: $employer-form-color-brighter;\n  font-size: 16px;\n  font-weight: 300;\n}\n\n@mixin typo-employer-form-button($usePreferred: true) {\n  @include typo-preferred-font($usePreferred);\n\n  font-size: 13px;\n  line-height: 13px;\n  font-weight: 400;\n  color: $color-white;\n}\n\n@mixin colored-button ($color) {\n  &.mdl-button--raised {\n    &,\n    &:hover {\n      background-color: $color;\n      color: $color-white;\n    }\n  }\n\n  & {\n    color: $color;\n  }\n}\n\n@mixin colored-radio ($color) {\n  .mdl-ripple {\n    background-color: rgba($color, 0.8);\n  }\n\n  &.is-checked {\n    .mdl-radio__outer-circle {\n      border-color: $color;\n    }\n\n    .mdl-radio__inner-circle {\n      background-color: $color;\n    }\n  }\n}\n\n@mixin colored-switch ($color) {\n  .mdl-ripple {\n    background-color: rgba($color, 0.8);\n  }\n\n  .mdl-switch__thumb {\n    background-color: #9e9e9e;\n  }\n\n  &.is-checked {\n    .mdl-switch__track {\n      background-color: rgba($color, 0.5);\n    }\n\n    .mdl-switch__thumb {\n      background-color: $color;\n    }\n  }\n}\n\n@mixin colored-checkbox ($color) {\n  .mdl-ripple {\n    background-color: rgba($color, 0.8);\n  }\n\n  &.is-checked {\n    .mdl-checkbox__tick-outline {\n      background-color: $color;\n    }\n\n    .mdl-checkbox__box-outline {\n      border-color: $color;\n    }\n  }\n}\n\n@mixin colored-icon-toggle ($color) {\n  &.is-checked .mdl-icon-toggle__label {\n    color: $color;\n  }\n\n  .mdl-ripple {\n    background-color: $color;\n  }\n}\n\n@mixin colored-progress($color) {\n  & > .progressbar.bar1 {\n    background-color: $color;\n  }\n\n  &.mdl-progress.mdl-progress__indeterminate > .auxbar {\n    background-image: none;\n    background-color: transparent;\n  }\n\n  @supports (-webkit-appearance:none) {\n    &.mdl-progress:not(.mdl-progress__indeterminate) > .auxbar {\n      background-image:\n        linear-gradient(to right, $progress-secondary-color, $progress-secondary-color),\n        linear-gradient(to right, $progress-secondary-color, $progress-secondary-color);\n      mask: url(\"data:image/svg+xml;base64,\");\n      -webkit-mask: url(\"data:image/svg+xml;base64,\");\n    }\n  }\n}\n\n@mixin colored-slider($color) {\n  &.is-upgraded {\n    color: $color;\n\n    &::-ms-fill-lower {\n      background:\n        linear-gradient(\n          to right,\n          transparent,\n          transparent 16px,\n          $color 16px,\n          $color 0\n        );\n    }\n\n    &::-webkit-slider-thumb {\n      background: $color;\n    }\n\n    &::-moz-range-thumb {\n      background: $color;\n    }\n\n    &::-ms-thum